import React, { useContext, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { PlanningPokerCards } from '../components/PlanningPokerCards';
import { UserAvatar } from '../components/UserAvatar';
import { SocketContext } from '../context/SocketContext';

export const PlanningPoker = (props) => {

  const { socket } = useContext(SocketContext);
  const { userName } = useParams();
  const [users, setUsers] = useState([]);
  const [showEffort, setShowEffort] = useState(false);

  useEffect(() => {
    socket.emit('select-user', { userName });
  }, [socket, userName]);

  useEffect(() => {
    socket.on('current-users', (users) => {
      setUsers(users);
    });
    socket.on('reveal-cards', (data) => {
      setShowEffort(data.reveal);
    });
    return () => socket.off('current-users');
  }, [socket]);

  return (
    <div>
      <br /><br />
      <PlanningPokerCards userName={userName} />
      <div className="avatar-list-box">
        {users.map((user) => {
          return <UserAvatar key={user.id} user={user} showEffort={showEffort} />
        })}
      </div>
    </div>
  )
}
