import React from 'react'
import { Avatar } from 'antd';

export const UserAvatar = ({ user, showEffort }) => {
  return (
    <div className="avatar-box">
      <Avatar size={64} className={user.effort ? 'avatar-check' : null} >{user.name}</Avatar>
      <div className='avatar-effort'>
        {showEffort && user.effort}
      </div>
    </div>
  )
}
