import { Col, Row } from 'antd'
import React, { useContext, useState } from 'react'
import { SocketContext } from '../context/SocketContext';
import { PokerCard } from './PokerCard'

export const PlanningPokerCards = ({ userName }) => {

  const { socket } = useContext(SocketContext);

  const [effort, setEffort] = useState();

  const changeEffort = (effort) => {
    setEffort(effort);
    socket.emit('set-effort', { userName, effort });
  }


  return (
    <>
      <Row>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('1')}>
          <PokerCard cardValue='1' active={effort === '1' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('2')}>
          <PokerCard cardValue='2' active={effort === '2' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('3')}>
          <PokerCard cardValue='3' active={effort === '3' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('5')}>
          <PokerCard cardValue='5' active={effort === '5' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('8')}>
          <PokerCard cardValue='8' active={effort === '8' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('13')}>
          <PokerCard cardValue='13' active={effort === '13' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('20')}>
          <PokerCard cardValue='20' active={effort === '20' ? true : false} />
        </Col>
        <Col xs={{ span: 6 }} lg={{ span: 3 }} className='poker-card-box' onClick={() => changeEffort('Coffe')}>
          <PokerCard cardValue='coffe' active={effort === 'Coffe' ? true : false} />
        </Col>
      </Row>
    </>
  )
}
